#magik-slideshow {
	border: none;
	padding: 0;
	margin: 20px auto;
	margin-bottom: 10px
}
#magik-slideshow .RHS-banner .add {
	margin-bottom: 12px;
	position: relative;
	overflow: hidden;
}
#magik-slideshow .RHS-banner .add img {
	-webkit-transition-duration: 500ms;
	transition-duration: 500ms;
	-webkit-transition-property: transform;
	transition-property: transform;
	-webkit-transform: translateZ(0);
	-moz-transform: translateZ(0);
	transform: translateZ(0);
}
#magik-slideshow .RHS-banner .add img:hover {
	background: #000;
	max-height: 750px;
	max-width: 100%;
	min-width: 100%;
	width: 100%;
	opacity: 0.8;
}
#magik-slideshow .RHS-banner .overlay {
	width: 100%;
	height: 100%;
	position: absolute;
	overflow: hidden;
	top: 0;
	left: 0px;
	text-align: center;
	background-color: rgba(0, 0, 0, 0.5);
	opacity: 0;
	transition: all 0.3s ease-in 0.4s;
}
#magik-slideshow .RHS-banner:hover .overlay {
	opacity: 1;
	transition-delay: 0s;
}
#magik-slideshow .RHS-banner .overlay:before, #magik-slideshow .RHS-banner .overlay:after {
	border: 1px solid #fff;
	-webkit-transform: scale(0, 1);
	transform: scale(0, 1);
	position: absolute;
	top: 30px;
	right: 30px;
	bottom: 30px;
	left: 30px;
	content: '';
	opacity: 0;
	-webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
	transition: opacity 0.35s, transform 0.35s;
	top: 10px;
	left: 10px;
	right: 10px;
	bottom: 10px;
	opacity: 1;
	-webkit-transform: scale(1);
	transform: scale(1);
	animation-delay: 0.3s;
	animation-name: fadeInUp;
}
#magik-slideshow .RHS-banner .overlay a.info {
	display: inline-block;
	text-decoration: none;
	padding: 15px 15px;
	background: #ff9800;
	color: #fff;
	font-family: 'Open Sans', sans-serif;
	font-weight: bold;
	font-size: 16px;
	text-transform: uppercase;
	top: 45%;
	position: relative;
	width: 100%;
	font-weight: 600;
	letter-spacing: 1px;
	z-index:1;
}
.rev_slider_wrapper {
	position: relative;
}
.rev_slider_wrapper >ul {
	visibility: hidden;
}
.rev_slider {
	position: relative;
	overflow: visible;
}
.rev_slider img {
	max-width: none !important;
	margin: 0px;
	padding: 0px;
	border-width: 0px;
	border: none;
	visibility: visible !important;
}
.rev_slider .no-slides-text {
	font-weight: bold;
	text-align: center;
	padding-top: 80px;
}
.rev_slider ul {
	margin: 0px;
	padding: 0px;
	list-style: none !important;
	list-style-type: none;
	background-position: 0px 0px;
}
.rev_slider ul li, .rev_slider >ul >li, .rev_slider >ul >li:before {
	list-style: none !important;
	position: absolute;
	visibility: hidden;
	margin: 0px !important;
	padding: 0px !important;
	overflow-x: visible;
	overflow-y: visible;
	list-style-type: none !important;
	background-image: none;
	background-position: 0px 0px;
	text-indent: 0em;
}
.rev_slider .tp-caption, .rev_slider .caption {
